.empty-wrap{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  .ant-empty{
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .ant-empty-image{
      margin: 0;
      img{
        -webkit-user-drag: none;
      }
    }
  }

}
